//child1.vue
&lt;template>
  &lt;div id="child1">
    &lt;h3>Child1&lt;/h3>
    getString::${getString} //1- props  String
    getObject::
    &lt;div v-for="item in getObject" :key="item.id">${item}}&lt;/div>
    //2- props  Object
    &lt;a href="#" @click="getFunction">&lt;/a> 
    //3- props  Function  @click 
  &lt;/div>
&lt;/template>
&lt;script>
  export default {
    name: "child1",
    props: {
      getString: {
        type: String, //1- String
        default: "default contry", //
        required: false //flase 
      },
      getObject: {
        type: Object, //2- Object
        default: function() {
          //
          return {
            name: "default name",
            description: "default description",
            price: 300.0
          };
        }
      },
      getFunction: {
        type: Function, //3- Function
        required: true //true 
      }
    }
  };
&lt;/script>
&lt;style>
  #child1 {
    background-color: rgb(124, 170, 255);
  }
&lt;/style>
//father.vue
&lt;template>
