    &lt;/ul>
    &lt;Child1 :getString="toString" :getObject="toObject" :getFunction="toFunction">&lt;/Child1>
    // 1-String2-Object3-Function
  &lt;Child1/div>
&lt;/template>
&lt;script>
import Child1 from "@/components/one_FTC/child1.vue";
export default {
  name: "father1",
  components: {
    Child1
  },
  data() {
    return {
      toString: "Your are Good", //1- String
      toObject: { //2- Object
