        description: "What a nice pen",
        price: 99.12
      }
    };
  },
  methods:{
    toFunction(){ //3- Function
     this.toString = 'Good to you'
    }
  }
};
&lt;/script>
&lt;style>
#father {
  background-color: orange;
}
ul {
  list-style: none;
}
