//main.js Vue 
import Vue from 'vue'
import router from './router' // router
import App from './App'

Vue.config.productionTip = false 
// false  vue 

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
//Home.vue view 
&lt;template>
  &lt;div>
    &lt;h3>Home&lt;/h3>
  &lt;/div>
&lt;/template>
&lt;script>
  export default {
    name: "Home"
  };
&lt;/script>
&lt;style>&lt;/style>
//router.js router 
import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home'
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
  ]
})
//App.vue
&lt;template>
  &lt;div>
    &lt;h3>App.vue&lt;/h3>
    &lt;div class="container">
      &lt;router-link :to="/">Home&lt;/router-link> //vue-router 
      &lt;Home>&lt;/Home> //
      &lt;router-view>&lt;/router-view> // router-link 
    &lt;/div>
  &lt;/div>
&lt;/template>
&lt;script>
  import Home from "@/views/Home.vue";
  export default {
    name: "App",
    component: {
      Home
    }
  };
&lt;/script>                
